import { ListComponent } from './list/list.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [

    {
        path: 'list',
        component: ListComponent,
        children: [
            {
                path:'',
                redirectTo:'gaji'
            },
            {
                path: 'gaji',
                loadChildren: () => import('./gaji/gaji.module').then(mo => mo.GajiModule),
            },
            {
                path: 'bonus',
                loadChildren: () => import('./bonus/bonus.module').then(mo => mo.BonusModule),
            },
            {
                path: 'onduty',
                loadChildren: () => import('./onduty/onduty.module').then(mo => mo.OndutyModule),
            },
            {
                path: 'thr',
                loadChildren: () => import('./thr/thr.module').then(mo => mo.ThrModule),
            },
            {
                path: 'pilot',
                loadChildren: () => import('./pilot/pilot.module').then(mo => mo.PilotModule),
            },
        ]
    },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class BankRoutingModule { }
